import { ethers } from 'ethers'

import {
    setProvider,
    setNetwork,
    setAccount
} from '../store/reducers/provider';

import {
    setContracts,
    setSymbols,
    balancesLoaded
} from '../store/reducers/tokens';
import {
    setContract,
    sharesLoaded,
    swapsLoaded,
    depositRequest,
    depositSuccess,
    depositFail,
    withdrawRequest,
    withdrawSuccess,
    withdrawFail,
    swapRequest,
    swapSuccess,
    swapFail
} from '../store/reducers/amm';

import TOKEN_ABI from "../abis/Token.json";
import AMM_ABI from "../abis/AMM.json";
import config from "../config.json"

export const loadProvider = (dispatch) => {
    const provider = new ethers.providers.Web3Provider(window.ethereum)
    dispatch(setProvider(provider))

    return provider
}

export const loadNetwork = async (provider, dispatch) => {
    const { chainId } = await provider.getNetwork()
    dispatch(setNetwork(chainId))
    console.log('contacts drop', chainId)
    return chainId
}

export const loadAccount = async (dispatch) => {
    // Fetch accounts
    const accounts = await window.ethereum.request({ method: 'eth_requestAccounts' })
    const account = ethers.utils.getAddress(accounts[0])
    dispatch(setAccount(account))
    console.log('load account', account)
    return account
}
//////////////////////
/// LOAD CONTRACTS ///
//////////////////////

export const loadTokens = async (provider, chainId, dispatch) => {
    const kal = new ethers.Contract(config[chainId].kal.address, TOKEN_ABI, provider)
    const dapp = new ethers.Contract(config[chainId].dapp.address, TOKEN_ABI, provider)

    dispatch(setContracts([kal, dapp]))
    dispatch(setSymbols([await kal.symbol(), await dapp.symbol()]))
}
export const loadAMM = async (provider, chainId, dispatch) => {
    const amm = new ethers.Contract(config[chainId].amm.address, AMM_ABI, provider)

    dispatch(setContract(amm))
    console.log('LOADamm', amm)
    return amm
}

//////////////////////////////
/// LOAD BALANCES & SHARES ///
//////////////////////////////

export const loadBalances = async (amm, tokens, account, dispatch, chainId) => {
    try {
        const balance1 = await tokens[0].balanceOf(account)
        const balance2 = await tokens[1].balanceOf(account)
        dispatch(balancesLoaded([
            ethers.utils.formatUnits(balance1.toString(), 'ether'),
            ethers.utils.formatUnits(balance2.toString(), 'ether')
        ]))
        console.log('BALANCESamm', amm, 'Balances and Shares Loaded for account:', account, 'chainId', chainId)

        const shares = await amm.shares(account)
        dispatch(sharesLoaded(ethers.utils.formatUnits(shares.toString(), 'ether')))
    } catch (error) {
        console.log('Failed to load balances or shares:', error)
    }
}
//////////////////////////////
//////// ADD LIQUIDITY ///////
//////////////////////////////
export const addLiquidity = async (provider, amm, tokens, amounts, dispatch) => {
    try {
        dispatch(depositRequest())
        const signer = await provider.getSigner()
        let transaction
        transaction = await tokens[0].connect(signer).approve(amm.address, amounts[0])
        await transaction.wait()
        transaction = await tokens[1].connect(signer).approve(amm.address, amounts[1])
        await transaction.wait()
        transaction = await amm.connect(signer).addLiquidity(amounts[0], amounts[1])
        await transaction.wait()
        dispatch(depositSuccess(transaction.hash))
        console.log('Liquidity Added:', transaction.hash)
    } catch (error) {
        console.log('Add Liquidity Failed:', error)
        dispatch(depositFail())
    }
}
//////////////////////////////
////// REMOVE LIQUIDITY //////
//////////////////////////////
export const removeLiquidity = async (provider, amm, shares, dispatch) => {
    try {
    dispatch(withdrawRequest())

    const signer = await provider.getSigner()

    let transaction = await amm.connect(signer).removeLiquidity(shares)
    await transaction.wait()

    dispatch(withdrawSuccess(transaction.hash))
    } catch (error) {
        dispatch(withdrawFail())
    }
}

//////////////////////////////
//////////// SWAP ////////////
//////////////////////////////

export const swap = async (provider, amm, token, symbol, amount, dispatch) => {
    try {
        dispatch(swapRequest())

        let transaction

        const signer = await provider.getSigner()

        transaction = await token.connect(signer).approve(amm.address, amount)
        await transaction.wait()

        if (symbol === 'KAL') {
            transaction = await amm.connect(signer).swapToken1(amount)
        } else {
            transaction = await amm.connect(signer).swapToken2(amount)
        }
        await transaction.wait()

        dispatch(swapSuccess(transaction.hash))
    } catch (error) {
        dispatch(swapFail())
    }

}

//////////////////////////////
////// LOAD ALL SWAPS ////////
//////////////////////////////

export const loadAllSwaps = async (provider, amm, dispatch) => {
    const block = await provider.getBlockNumber()
  
    const swapStream = await amm.queryFilter('Swap', 0, block)
    const swaps = swapStream.map(event => {
      return { hash: event.transactionHash, args: event.args }
    })
  
    dispatch(swapsLoaded(swaps))
  }
  